import { Link } from "react-router-dom";
import style from "./Header.module.css";
import Search from "../Search";

export default function Header() {
  return (
    <>
      <div className={style.container}>
        <div className={`${style.lista} ${style.home}`}>
          <Link to="/">
            <img
              className={style.homeimg}
              src="https://uploads.turbologo.com/uploads/design/hq_preview_image/5097676/draw_svg20210617-26747-23281c.svg.png"
            ></img>
          </Link>
        </div>

        <ul className={`${style.lista} ${style.characters}`}>
          <li>
            <Link to="/characters/all">
              All Characters
              <img
                className={style.logo}
                src="data:image/jpeg;base64,"
              ></img>
            </Link>
          </li>
          <li>
            <Link to="/characters/students">
              Students
              <img
                className={style.logo}
                src="https://www.clarin.com/img/2022/01/01/la-saga-de-harry-potter___Yc21zA-Vt_340x340__1.jpg"
                alt=""
              />
            </Link>
          </li>
          <li>
            <Link to="/characters/staff">
              Staff
              <img
                className={style.logo}
                src="https://vignette.wikia.nocookie.net/7123a85d-573a-4b08-8780-789e9fe7708d/scale-to-width-down/1200"
              ></img>
            </Link>
          </li>
        </ul>

        <ul className={`${style.lista} ${style.casas}`}>
          <li>
            <Link to="/characters/Gryffindor">
              <img
                className={`${style.logo} ${style.imgcasas}`}
                src="data:image/jpeg;base64,"
                alt="Gryffindor Logo"
              />
              Gryffindor
            </Link>
          </li>
          <li>
            <Link to="/characters/Hufflepuff">
              <img
                className={`${style.logo} ${style.imgcasas}`}
                src="data:image/jpeg;base64,"
                alt="Hufflepuff Logo"
              />
              Hufflepuff
            </Link>
          </li>
          <li>
            <Link to="/characters/Ravenclaw">
              <img
                className={`${style.logo} ${style.imgcasas}`}
                src="data:image/jpeg;base64,"
                alt="Ravenclaw Logo"
              />
              Ravenclaw
            </Link>
          </li>

          <li>
            <Link to="/characters/Slytherin">
              <img
                className={`${style.logo} ${style.imgcasas}`}
                src="data:image/jpeg;base64,"
                alt="Slytherin Logo"
              />
              Slytherin
            </Link>
          </li>
        </ul>
      </div>
      <Search />
    </>
  );
}
